import * as split from 'lodash/split';
import * as memoize from 'lodash/memoize';
import * as fpPipe from 'lodash/fp/pipe';
import * as fpToPairs from 'lodash/fp/toPairs';
import * as fpMap from 'lodash/fp/map';
import * as fpSortBy from 'lodash/fp/sortBy';

function obj2Array(mapDataIndex) {
  return fpPipe(
    fpToPairs,
    fpMap(([name, groupObj]) => ({
      name,
      children: fpPipe(
        fpToPairs,
        fpMap(['name', 'path']),
        fpSortBy('name')
        )(groupObj)
    }))
  )(mapDataIndex);
}

function getByGeoType(mapDataIndex) {
  return geoType => {
    const [type] = split(geoType, ':');
    switch (type) {
    case 'country':
      return obj2Array(mapDataIndex['Custom']);
    case 'state':
      return [{
        name: 'Countries',
        children: [{
          name: 'United States of America',
          path: 'countries/us/us-all.geo.json'
        }]
      }];
    case 'county':
      return obj2Array(mapDataIndex['USA States']);
    case 'zip':
      return [];
    }
  };
}

